@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
#codare-content{
    width: 80%;
    min-height: 100vh;
    margin-left: calc(20% + 40px);
    overflow: auto;
}
#codare-main-banner{
    width: 100%;
    border-radius: 5px;
    background-image: url(/integrando-api-no-front/static/media/main-banner.b43a3823.svg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    height: 700px;
    padding-left: 100px;

    display: flex;
    justify-content: left;
    align-items: center;
}

#codare-main-banner .codare-featured-playlist{
    font-weight: 700;
    font-size: 70px;
    width: 100%;
}

#codare-main-banner .codare-featured-playlist-description{
    font-weight: 500;
    font-size: 24px;
}

#codare-featured-button{
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    background: linear-gradient(90deg, #8A30FD 0%, #7000FF 100%), #2C575A;
    border-radius: 5px;
    width: 193px;
    height: 56px;
    left: 0px;
    top: 0px;
    text-align: center;
    line-height: 56px;
    margin-top: 10px;
    cursor: pointer;
}
.codare-shelf-item{
    min-height: 400px;
    width: 280px;
    max-width: 100%;
    margin-right: 20px;
    position: relative;
}

.codare-shelf-item:last-child{
    margin-right: none;
}

.codare-overlay{
    transition: opacity 0.5s ease;
    -webkit-transition: opacity 0.5s ease;
    -moz-transition: opacity 0.5s ease;
    position: relative;
}

.codare-overlay:after {
    content: '\A';
    position: absolute;
    width: 273px;
    margin-left: 3px;
    height: calc(100% - 7px);
    margin-top: 4px;
    border-radius: 5px;
    top:0; 
    left:0;
    background:rgba(0,0,0,0.2);
    opacity: 1;
    transition: opacity 0s;
    -webkit-transition: opacity 0s;
    -moz-transition: opacity 0s;
}
.codare-overlay:hover:after {
    opacity: 0;
}

.codare-shelf-item .codare-cover{
    background-position: center center;
    background-repeat: no-repeat;
    background-image: url(/integrando-api-no-front/static/media/album-cover.9fabdbe5.svg);
    width: 280px;
    height: 280px;
    border-radius: 5px;
}

.codare-shelf-item .codare-title{
    color: var(--white);
    font-weight: 500;
    height: 25px;
    line-height: 25px;  
    font-size: 23px;  
    margin-left: 5px;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-top: 15px;
}

.codare-shelf-item .codare-title:hover{
    color: var(--six-purple);
    font-weight: 600;
    cursor: pointer;
}

.codare-shelf-item .codare-subtitle{
    position: relative;
    color: var(--white);
    font-weight: 300;
    height: 50px;
    width: calc(100% - 10px);
    line-height: 20px;  
    font-size: 15px;  
    margin-left: 5px;
    margin-top: 20px;
    overflow: hidden;
}
.codare-shelf-container{
    width: 100%;
    height: 500px;
    position: relative;
}

.codare-shelf{
    margin-top: 20px;
    height: 100px;
    max-width: 100%;
    overflow: hidden;
}

.codare-shelf .title{
    font-weight: 700;
    font-style: normal;
    font-size: 32px;
}

.codare-shelf .subtitle{
    font-weight: 200;
    font-size: 18px;  
    line-height: 22px; 
    height: 22px;
    margin-top: 5px;
}

.codare-shelf-items{
    width: 100%;
    height: 420px;
    padding-top: 5px;
    position: absolute;
}

.codare-icon-item{
    font-family: Montserrat;
    font-style: normal;
    font-weight: 200;
    font-size: 18px;
    line-height: 22px;
    height: 22px;
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
}

.codare-icon-item:hover{
    cursor: pointer;
    font-weight: 300;
}

.codare-icon-item .codare-icon{
    width: 22px;
    height: 22px;
    margin-right: 20px;
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
}

.codare-icon-item .codare-text{
    position: relative;
    margin-top: 2px;
}
.codare-main-title{
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
    cursor: default;
}
.codare-simple-item{
    font-weight: 200;
    color: var(--white);
    font-size: 18px;
    line-height: 22px;
    margin-top: 20px;
    cursor: pointer;
}

.codare-simple-item:hover{
    font-weight: 300;
}
.codare-secondary-title{
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    margin-top: 20px;
    font-style: normal;
    position: relative;
    display: flex;
}

.codare-secondary-title:hover{
    cursor: pointer;
    color: var(--six-purple);
}

.codare-secondary-title .codare-arrow{
    background-image: url(/integrando-api-no-front/static/media/menu-arrow.76afa330.svg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 18px;
    height: 18px;
    position: absolute;
    right: 0;
    transition: 100ms;
}

.codare-secondary-title .codare-arrow.opened{
    transform: rotate(180deg);
    transition: 100ms;
}

.codare-items{
    background-color: rebeccapurple;
}

.codare-items-enter {
    opacity: 0;
}

.codare-items-enter-active {
    opacity: 1;
    transition: opacity 100ms;
}

.codare-items-exit {
    opacity: 1;
}
.codare-items-exit-active {
    opacity: 0;
    transition: opacity 100ms;
}
#codare-menu{
    background-color: var(--two-gray);
    width: 100%;
    border-radius: 5px;
    padding: 20px;
}

#codare-menu:hover{
    background-color: var(--two-gray-hover);
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
#codare-sidebar{
    width: 20%;
    height: 100%;
    position: fixed;
    z-index: 11;
    overflow: auto;
    max-height: 82vh;
}

#codare-sidebar::-webkit-scrollbar { 
    display: none;
}

#codare-body{
    width: 100%;
    min-height: 200vh;
    position: relative;
    z-index: 9;
    height: 100px;
    background-color: var(--one-gray);
    color: var(--white);
    padding: 20px;
    padding-top: 140px;
    display: flex;
}
#codare-footer{
    position: relative;
    z-index: 8;
    background-color:var(--five-gray);
    height: 85px;
    width: 100%;
    color: var(--white);
    text-align: center;
    line-height: 80px;
}
#codare-header{
    width: 100%;
    height: 120px;
    background-color: var(--default-gray);
    box-shadow: 0px 10px 20px rgba(21, 19, 21, 0.4);
    position: fixed;
    z-index: 10;
    top: 0;
}

#codare-header #codare-logo{
    position: relative;
    width: 200px;
    height: 100px;
    background-image: url(/integrando-api-no-front/static/media/codare-music-logo.a1e0784f.svg);
    background-position: center center;
    background-repeat: no-repeat;
    margin: auto;
    margin-top: 10px;
}

