*::-webkit-scrollbar-track
{
	box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: var(--default-gray);
}

*::-webkit-scrollbar
{
	width: 12px;
	background-color: var(--default-gray);
}

*::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: var(--one-gray);
}
/* CSS Variables */

:root {
    --default-gray: #292929;
    --one-gray: #1E1E1F;
    --two-gray: #292929;
    --two-gray-hover: #2c2b2e;
    --five-gray: #141415;

    --white: #FFF;

    --six-purple: #BC87FF;

}
